import React from 'react';
import { Router,Route,Routes  } from "react-router-dom";
import Login from "./Login"
class RouterComponent extends React.Component {
    constructor(props){
        super(props)
    }
    render() {
      return (
          <Router>
            <Routes>
                    <Route exact path="/" element={<Login/>} />
            </Routes>
          </Router>
      );
    }
  }
  
  export default RouterComponent;